    /*#__PURE__*/_createElementVNode("p", null, " 我在城市的时间里看到时间是属于摇滚乐的，一年前还是土堆成山的地方，呼啦啦拔地而起一片楼群;又见拆迁的工地，昔日的楼房破腔露肚，灰白的墙壁上满是雨痕。重建与摧毁，搬入与流离，过去与现在宛如时间的两排利齿，一切都被咬得粉碎。 "),
