    /*#__PURE__*/_createElementVNode("p", null, " 我从一个城市迁徙到另一个城市，舍弃旧的人事，建立新的人事，流动的、变化的，没有一个坚硬的空间能顽强留存。 "),
    /*#__PURE__*/_createElementVNode("p", null, " 时间在村庄没有痕迹吗?试想我与相差二十岁的侄子，我跟他在同样的村庄长大，看到的同样是田地、池塘、泥路，同样可以攀在江边的桑树上吃桑葚，然而这二十年的时间是虚无的吗?我看着他跟小时候的我一样用瓦片和泥土玩过家家，一样看到黄昏时太阳在田野尽头的树林间隐没。然而，他再也听不到每天早上一直喊到我起床的米糕小贩的叫卖声了，再也听不到敲着清脆响亮铁板卖姜糖的叮叮当声了，再也不会跟我一样挤坐在垸礼堂听戏了。手工艺人在农村已经消失，无论是篾匠、木匠，还是弹棉花的匠人，都已经无处寻觅。手工艺人展现技艺的时间感是缓慢的、耐心的。我记得，随着拨浪鼓的咚咚声，婶娘们拥出门围着小贩买针头线脑，而满身棉絮的匠人在堂屋用巨大的弓弹着棉花，宛如翻搅起漫天雪花。 "),
    /*#__PURE__*/_createElementVNode("p", null, " 而我的侄子只能看到事物的最终状态，时间在“需求—供应”模式下被挤压成薄片。他睡在从家居市场买来的床上，吃着从菜市场买来的菜，玩着从超市买来的玩具。虽在农村，却与在城市几无差异，还好他能看到跑动的鸡和狗，认识生长在田地里的棉花和小麦。 "),
    /*#__PURE__*/_createElementVNode("p", null, " 他跟随他的父母离开村庄，进入不同的城市，不断变更就读的学校，不断认识新同学又忘记老同学。这样一种流动不居的空间变动，给他带来的是怎样的时间体验呢?我想在我之前，祖辈都在这个村庄日出而作、日落而息，耕种同一片土地，喝同一脉井水，我想时间于他们是绵长的、悠远的。而到了我这一代，空间开始变动，时间慢慢加快，村庄中的人不断外涌。由此我看到在我父亲与他的父亲之间，时间是没有肌理的;而在我跟我侄子之间，时间裹挟的人事变化远超从前。 "),
